// extract
import topUp from "@/assets/topUpIcon.png";
import extract from "@/assets/extractIcon.png";
import btc from "@/assets/btc.png";
import bch from "@/assets/bch.png";
import cnc from "@/assets/cnc.png";
import usdt from "@/assets/usdt.png";
export default {
  data() {
    return {
      // isLink:true,
      btc: btc,
      cnc: cnc,
      usdt: usdt,
      extract: extract,
      topUp: topUp,
      topTitle: "我的钱包",
      btc_total: "0",
      btc_total_rmb: "0.0",
      list: [
        {
          img: btc,
          name: "BTC",
          use: "0",
          rem: "0.0",
          freeze: "0.00000000",
          total: "0",
          chain: "",
          coin_type: ""
        },
        {
          img: cnc,
          name: "CNC",
          use: "0",
          rem: "0.0",
          freeze: "0.00000000",
          total: "0",
          chain: "",
          coin_type: ""
        },
        {
          img: usdt,
          name: "USDT",
          use: "0",
          rem: "0.0",
          freeze: "0.00000000",
          total: "0",
          chain: "",
          coin_type: ""
        },
        {
          img: bch,
          name: "BCH",
          use: "0",
          rem: "0.0",
          freeze: "0.00000000",
          total: "0",
          chain: "",
          coin_type: ""
        }
      ]
    };
  },
  mounted() {
    this.getWalletBalance();
  },
  methods: {
    getWalletBalance() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/wallet/wallet-balance",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.btc_total = response.data.btc_total;
          that.btc_total_rmb = response.data.btc_total_rmb;
          // BTC
          that.list[0].use = response.data.btc_btc;
          that.list[0].rem = parseFloat(response.data.btc_btc_rmb.toFixed(8));
          that.list[0].total = response.data.btc_btc;
          that.list[0].chain = response.data.btc_chain;
          that.list[0].coin_type = response.data.btc_coin_type;
          //CNC
          that.list[1].use = response.data.cnc_bts;
          that.list[1].rem = parseFloat(response.data.cnc_bts_rmb.toFixed(8));
          that.list[1].total = response.data.cnc_bts;
          that.list[1].chain = response.data.cnc_chain;
          that.list[1].coin_type = response.data.cnc_coin_type;
          //USDT
          that.list[2].use = response.data.usdt_eth;
          that.list[2].rem = parseFloat(response.data.usdt_eth_rmb.toFixed(8));
          that.list[2].total = response.data.usdt_eth;
          that.list[2].chain = response.data.usdt_chain;
          that.list[2].coin_type = response.data.usdt_coin_type;
          //BCH
          that.list[3].use = response.data.bch_bch;
          that.list[3].rem = parseFloat(response.data.bch_bch_rmb.toFixed(8));
          that.list[3].total = response.data.bch_bch;
          that.list[3].chain = response.data.bch_chain;
          that.list[3].coin_type = response.data.bch_coin_type;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getTB() {
      this.$router.push("/wallet/extractMoney");
    },
    getCB() {
      this.$router.push("/wallet/chanrigMoney");
    },
    getDataVal(val) {
      this.$router.push({
        name: "myWallet",
        params: {
          itm: val
        }
      });
    }
  }
};



